:root {
    --color-primary: #2584ff;
    --color-secondary:#00d9ff;
    --color-accent: #ff3400;
    --color-heading: #1b0760;
    --color-body: #918ca4;
    --color-body-darker: #5c5577;
    --color-border:#ccc;
    --border-radius: 30px;
}

/*Typography*/

::selection {
    background: var(--color-primary);
    color: #fff;
}
html {
    font-size: 62.5%; /*= 10px*/
}

/*universal selector, for adjust size of the button,... fit into the page*/

*,
*::after,
*::before {
    box-sizing: border-box;
}

body {
    font-family: Inter, Arial, Helvetica, sans-serif;
    font-size: 2.4rem;
    line-height: 1.5; /*one and half size of current font size*/
    color: var(--color-body);
}

h1, h2, h3 {
    color: var(--color-heading);
    margin-bottom: 1rem;
    line-height: 1.1;
}

h1 {
    font-size: 7rem;
}

h2 {
    font-size: 4rem;
}

h3 {
    font-size: 3rem;
}

p {
    margin-top: 0;
}

@media screen and (min-width:1024px){
    body {
        font-size: 1.8rem;
    }
    h1 {
        font-size:8rem;
    }
    h2 {
        font-size: 4rem;
    }
    h3 {
        font-size: 2.4rem;
        font-weight: 500;
    }
}
/*Link*/
a {
    text-decoration: none;

}

.link-arrow {
    color: var(--color-accent);
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: bold;
}

.link-arrow::after{
    content: "-->";
    margin-left: 5px;
    transition: margin 0.15s;
}

.link-arrow:hover::after {
    margin-left: 10px;
}

@media screen and (min-width:1024px) {
    .link-arrow {
        font-size: 1.5rem;
    }
    
}

/*Badges*/

.badge {
    border-radius:var(--border-radius);
    padding: 0.5rem 2rem;
    font-weight: 600;
    white-space: nowrap;
    font-size: 2rem;
}

.badge--primary {
    background-color: var(--color-primary);
    color:  #fff;
}

.badge--secondary {
    background-color: var(--color-secondary);
    color:#fff
}

.badge--small {
    font-size: 1.5rem;
    padding: 0.5rem 1.5rem;
}

@media screen and (min-width:1024px) {
    .badge {
        font-size: 1.5rem;
    }
    .badge--small {
        font-size: 1.2rem;
    }
}

/*List*/

.list {
    list-style: none;
    padding: 0;
}

.list--inline .list__item{
    display:inline-block;
    margin-right: 2rem;
}

.list--tick {
    list-style-image: url(../images/tick.svg);
    padding-left: 3rem;
    color: var(--color-heading);
}

.list--tick .list__item {
    padding-left: 0.5rem;
    margin-bottom: 1rem;
}

@media screen and (min-width: 1024px) {
    .list--tick .list__item {
        padding-left: 0;
    }
    
}

/*icon*/
.icon {
    width: 30px;
    height: 30px;
}

.icon--small {
    width: 30px;
    height: 30px;
}

.icon--white {
    fill: #fff;
}

.icon--primary {
    fill: var(--color-primary);
}

.icon-container {
    background: #f3f9fa;
    width: 64px;
    height: 64px;
    border-radius: var(--border-radius);
    display: inline-flex;
    justify-content: center; /*align in x direction*/
    align-items: center;     /*align in second --> y direction  */
}

.icon-container--accent {
    background: var(--color-accent);
}
/*Button*/

.btn {
    font-size: 1.8rem;
    font-weight: 600;
    text-transform: uppercase;
    padding: 2rem 3rem;
    outline: 0;
    border: 0;
    border-radius: var(--border-radius);
    margin: 1rem;
    text-align: center;
    cursor: pointer;
    white-space: nowrap;
}

.btn .icon {
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
    vertical-align: middle;
}

.btn--primary {
    background: var(--color-primary);
    color: #fff; /* text color */
}

.btn--primary:hover {
    background: #134280;
 }

.btn--secondary {
    background: var(--color-secondary);
    color: #fff;
}

.btn--secodary:hover {
    background: #098196;
}

.btn--accent {
    background: var(--color-accent);
    color:#fff;
}

.btn--accent:hover  {
    background: #e7192a;
}

.btn--stretched {
    padding-left: 6rem;
    padding-right: 6rem;
}

@media screen and (min-width: 768px) {

    .callout .grid--1x2 {
        grid-template-columns: 1fr auto; /*allocated the extra space to the first coulmn*/
    }
    .callout__content {
        text-align: left;
    }
    .callout .btn {
        justify-self: start;
        margin: 2rem;
    }
    
}

.btn--block {
    width:100%;
    display: inline-block;
    color:#03010a;
}

.btn--block:hover {
    background: #fff;
    color:#03010a
}

.btn--outline {
    border: 2px solid #03010a;
    background: #fff;
}

@media screen and (min-width:1024px) {
    .btn {
        font-size: 1.5rem;
    }
}


/*input*/
.input {
    border-radius: var(--border-radius);
    border: 1px solid var(--color-border);
    color: var(--color-heading);
    font-size: 2rem;
    outline: 0;
    padding: 1.5rem 3.5rem;
}

::placeholder {
    color:#cdcbd7;
    
}

.input-group {
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius);
    display: flex;
    /*width: 50%;*/
}

/*nesting*/

.input-group .input {
    border:0;
    flex-grow: 1;
    padding:1.5rem 2rem;
    width: 0;
}

.input-group .btn {
    margin:4px;
}
@media screen and (min-width:1024px){
    .input {
        font-size: 1.5rem;
    }
    
}

/*Cards*/

.card {
    border-radius: 7px;
    box-shadow: 0 0 20px 10px #f3f3f3; /*horizontal vertical  blouring spreading color*/
    overflow: hidden; /*this solve conflict between child and parent, here header and container*/
}

.card__header,
.card__body {
    padding: 2rem 3rem;
}

/*--primary and --secondary are used for skin*/

.card--primary .card__header{
    background: var(--color-primary);
    color:#fff;
}

.card--secondary .card__header{
    background: var(--color-secondary);
    color:#fff;
}

.card--primary .badge--primary {
    background: #126de4;
}

.card--secondary .badge--secondary {
    background: #02cdf1;
}

/*Plan*/

.plan {
    transition: transform 0.2s ease-out;
}
.plan__name {
    color: #fff;
    margin:0;
    font-weight: 500;
    font-size: 2.4rem;
}

.plan__price {
    font-size: 6rem;
}

.plan__billing-cycle {
    font-size: 2.4rem;
    font-weight: 300;
    opacity: 0.8;
    margin-right: 1rem;
}

.plan__description {
    font-size: 2rem;
    font-weight: 300;
    letter-spacing: 1px;
    display: block;
}

.plan .list__item {
    margin-bottom: 2rem;
    letter-spacing: 1px;
}


.plan--popular {
    transform: scale(1.1);
}
.plan--popular .card__header {
    position: relative;
}
.plan--popular .card__header::before {
    content: url(../images/popular.svg);
    width: 40px;
    display: inline-block;
    position: absolute;
    top: 0;
    right: 5%;
}

.plan:hover {
    transform: scale(1.05);
}

.plan--popular:hover {
    transform: scale(1.15);
}

@media screen and (min-width: 1024px) {
    .plan__name {
        font-size: 1.4rem;
    }

    .plan__price {
        font-size: 5rem;
    }

    .plan__billing-cycle {
        font-size: 1.6rem;
    }
    .plan__description {
        font-size: 1.7rem;
    }
}

/*Media*/

.media {
    display: flex;
    margin-bottom: 4em;
}

.media__title {
    margin-top: 0;
}

.media__body {
    margin:0 2rem;
}

.media__image {
    margin-top: 1rem;
}

/*Quote*/

.quote {
    font-size: 3rem;
    font-style: italic;
    color: var(--color-body-darker);
    line-height: 1.3px;
}

.quote__text::before {
    content: open-quote;
}

.quote__text::after {
    content: close-quote;
}

.quote__author {
    font-size: 3rem;
    font-weight: 500;
    font-style: normal;
    font-family: monospace;
    margin-bottom: 0;
}

.quote__organization {
    color: var(--color-heading);
    font-size: 2rem;
    font-style: normal;
    opacity: 0.4;
}

.quote__line {
    position: relative;
    bottom: 10px;
}


@media screen and (min-width: 1024px) {
    .quote {
        font-size: 2rem;
    }
    .quote__author {
        font-size: 2.4rem;
    }
    .quote__organization {
        font-size: 1.6rem;
    }
    
}

/*grid*/

.grid {
    display: grid;
}

@media screen and (min-width: 768px) {
    .grid--1x2 {
        grid-template-columns: 1fr 1fr; /*repeat(2,1fr)*/
    }
}

@media screen and (min-width: 1024px) {
    .grid--1x3 {
        grid-template-columns: 1fr 1fr 1fr; /*repeat(3,1fr)*/
    }
}

/*testimonial*/

.testimonial {
    padding: 3rem;

}

.testimonial__image-container  .testimonial_image{  
    width: 100%;
    padding: 5rem;
}

.testimonial__image-container {
    position: relative;
}


.testimonial__image-container .icon-container {
    position: absolute;
    top: 80px;
    right: 20px;
    overflow: hidden;
}

@media screen and (min-width:768px) {
    .testimonial .quote,
    .testimonial .quote__author {
        font-size: 2.4rem;
    }
}

.testimonial .quote {
    margin-left: 6rem;
    margin-top: 6rem;
}


/*callout*/

.callout {
    padding: 4rem;
    border-radius: 5px;
}

.callout--primary {
    background: var(--color-primary);
    color: #fff;
}

.callout__heading {
    color: #fff;
    margin-top: 0;
    font-size: 3rem;
}

.callout .btn {
    justify-self: center;
    align-self: center;
}

.callout__content {
    text-align: center;
}
/*collapsible__header*/

.collapsible__header {
    display: flex;
    justify-content: space-between;  /*push the icon to the right end*/
}

.collapsible__heading {
    margin-top: 0;
    font-size: 3rem;
}

.collapsible__chevron {
    transform: rotate(-90deg);
    transition: transform 0.3s; /*for animate properties*/
    
}

.collapsible__content {
    max-height: 0;   /*to make an animation effect (see down)*/
    overflow: hidden;
    opacity: 0;
    transition: all 0.3s;
}

.collapsible--expanded .collapsible__chevron{
    transform: rotate(0);

}

.collapsible--expanded .collapsible__content {
    max-height: 100vh;  /*to make an animation effect (see below)*/
    opacity: 1;
}

/*Blocks*/

.block{
    --padding-vertical: 6rem;
    padding: var(--padding-vertical) 2rem;
    /*border: 2px solid red;*/
}

.block__header {
    text-align: center;
    margin-bottom: 4rem;
}

.block__heading {
    margin-top: 0;
}

.block--dark {
    background: #000;
    color: #7b858b;
}

.block--dark h1,
.block--dark h2,
.block--dark h3 {
    color:#fff;
}
.block--skewed-right {
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 90%); /*top-left  top-right  down-left  down-right*/

}

.container {
    max-width: 1140px; /* just for here*/
    margin: 0 auto; /*vertical horisontal*/
}

.block--skewed-left {
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0% 0%, 100% 0%, 100% 80%, 0% 100%);

}

/* Navigation Bar*/

.nav {
    background: #000;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 1rem;
    align-items: center;
}

.nav__list {
    width: 100%;
    margin: 0;
    max-height: 100%;
    opacity: 1;
}

.nav__item {
    padding: 0.5rem 2rem;
    border-bottom: 1px solid #222;
    
}

.nav__item > a { /* direct child selector*/
    color: #d2d0db;
    transition: color 0.3s;

}

/*.nav__brand { 
    color: #d2d0db;

}*/

.nav__brand {
    transform: translateY(5px);
}

.nav__item > a :hover { 
    color: #fff;

}

.nav__toggler {
    opacity: 0.5;
    transition: box-shadow 0.15s;
    cursor: pointer;
}

/*attach two classes to each other (double class selector)  --> .nav.collapsible--expanded*/
.nav.collapsible--expanded .nav__toggler { /*we want to use only this properties for .nav*/
    opacity: 1;
    box-shadow: 0 0 0 3px #666; /*H V bluring*/
    border-radius: 5px;
}

@media screen and (min-width:768px) {
    .nav__toggler {
        display: none;
    }
    .nav__list {
        width: auto;
        display: flex;
        font-size: 1.6rem;
    }
    .nav__item {
        border: 0;
    }
    
}

/*Hero*/

.hero {
    clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);

}
.hero__tagline {
    font-size: 2rem;
    color: #b9c3cf;
    letter-spacing: 1px;
    margin: 2rem 0 5rem;

}

.hero__image {
    width: 100%;
}

@media screen and (min-width:768px) {
    .hero {
        padding-top: 0;
    }
    .hero__content {
        text-align: left;
        align-self: center;
    }
    
}

/*Domain Block*/

.block-domain .input-group {
    box-shadow: 0 0 30px 20px #e6ebee;
    border: 0;
    margin: 4rem auto;
    max-width: 670px;
}

.block-domain__prices {
    color: var(--color-heading);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 6rem);
    font-size: 2rem;
    font-weight: 600;
    justify-items: center;
    max-width: 700px;
    margin: 0 auto;
}

@media screen and (min-width:768px) {
    .block-domain__prices {
        grid-template-columns: repeat(auto-fit, minmax(100px, 1fr)); /*one can fit as many coulomn as needed to fit all items*/
    }
    
}

/*Plans Block*/

.block-plans .grid {
    gap: 8rem 4rem;
}

.block-plans .card {
    max-width: 500px;
    margin: 0 auto;  /*place the cards in center for mobile version*/
}

/*Feature*/

.feature {
    gap: 4rem 2rem;
    margin: 12rem 0;
}
.feature:first-of-type {
    margin-top: 6rem;
}

.featue__heading {
    margin: 1rem 0;
}

.feature__image {
    width: 100%;
}

@media screen and (min-width:768px) {
    .feature:nth-of-type(even) .feature__content{ /*use psudo class selector to all same classes*/

        order:2;
    }
    
}

/*showcase Block*/

.block-showcase__image > img{
    width: 100%;
}

@media screen and (min-width: 768px) {
    .block-showcase .grid {
        grid-template-columns: 50% 50%;  /* different results 1fr 1fr*/
    }
    .block-showcase__image {
       
        justify-self: end;
    }
.block-showcase__image > img{
    width: auto;
    max-width: 700px;
}

    
}

/*Footer*/

.footer {
    background: #232323;
}

footer a {
    color: #777;
}

footer a:hover {
    color: #fff;
    transition: color 0.3s;
}

.footer__section {
    padding: 2rem;
    border-bottom: 1px solid #393939;
}

.footer__section .list {
    margin: 0;
}

.footer__heading {
    text-transform: uppercase;
    font-weight: 600;
}

.footer__brand {
    margin-top: 5rem;
    text-align: center;
}

.footer__brand img {
    width: 100%;
    max-width: 230px;
}

.ffoter__copyright {
    font-size: 2.1rem;
    color: #fff;
    opacity: 0.3;
}

@media screen and (min-width:768px) {
    .footer__sections {
        grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));
    }
    .footer .collapsible__chevron {
        display: none;
    }
    .footer .collapsible__content {
        opacity: 1;
        max-height: 100%;
    }
    .footer__brand {
        order: -1;
        margin-top: 1rem;
    }
    .footer__copyright {
        font-size: 1.5rem;
    }
    .footer__section {
        border: 0;
    }
    .footer__heading {
        font-size: 1.6rem;
    }

    
}